import React, {
  Fragment,
  useState,
  useRef,
  useCallback,
  useMemo,
  Children,
} from "react";
import * as styles from "./heroSlider.module.css";

export default function HeroSlider({
  style = {},
  autoFill = false,
  play = true,
  pauseOnHover = true,
  pauseOnClick = true,
  direction = "left",
  speed = 50,
  delay = 0,
  loop = 0,
  children,
}) {
  const [containerWidth, setContainerWidth] = useState(0);
  const [marqueeWidth, setMarqueeWidth] = useState(0);
  const [multiplier, setMultiplier] = useState(1);
  const [isHover, setIsHover] = useState(false);
  // const [cursorPos, setCursorPos] = useState({ top: 0, left: 0 });
  const rootRef = useRef(null);
  const containerRef = useRef(null) || rootRef;
  const marqueeRef = useRef(null);

  // const onMouseMove = (e) => setCursorPos({ top: e.screenY, left: e.screenX });

  //   Calculate width of container and marquee and set multiplier
  const calculateWidth = useCallback(() => {
    if (marqueeRef.current && containerRef.current) {
      const containerRect = containerRef.current.getBoundingClientRect();
      const marqueeRect = marqueeRef.current.getBoundingClientRect();
      let containerWidth = containerRect.width;
      let marqueeWidth = marqueeRect.width;

      if (autoFill && containerWidth && marqueeWidth) {
        setMultiplier(
          marqueeWidth < containerWidth
            ? Math.ceil(containerWidth / marqueeWidth)
            : 1
        );
      } else {
        setMultiplier(1);
      }

      setContainerWidth(containerWidth);
      setMarqueeWidth(marqueeWidth);
    }
  }, [autoFill, containerRef, direction]);

  // Calculate width and multiplier on mount and on window resize
  // useEffect(() => {
  //   if (!isMounted) return;

  //   calculateWidth();
  //   if (marqueeRef.current && containerRef.current) {
  //     const resizeObserver = new ResizeObserver(() => calculateWidth());
  //     resizeObserver.observe(containerRef.current);
  //     resizeObserver.observe(marqueeRef.current);
  //     return () => {
  //       if (!resizeObserver) return;
  //       resizeObserver.disconnect();
  //     };
  //   }
  // }, [calculateWidth, containerRef, isMounted]);

  // // Recalculate width when children change
  // useEffect(() => {
  //   calculateWidth();
  // }, [calculateWidth, children]);

  // useEffect(() => {
  //   setIsMounted(true);
  // }, []);

  // Animation duration
  const duration = useMemo(() => {
    if (autoFill) {
      return (marqueeWidth * multiplier) / speed;
    } else {
      return marqueeWidth < containerWidth
        ? containerWidth / speed
        : marqueeWidth / speed;
    }
  }, [autoFill, containerWidth, marqueeWidth, multiplier, speed]);

  // Render {multiplier} number of children
  const multiplyChildren = useCallback(
    (multiplier) => {
      return [
        ...Array(
          Number.isFinite(multiplier) && multiplier >= 0 ? multiplier : 0
        ),
      ].map((_, i) => (
        <Fragment key={i}>
          {Children.map(children, (child) => {
            return (
              <div
                className={`${styles.child}`}
                onMouseOver={() => setIsHover(true)}
                onMouseOut={() => setIsHover(false)}
              >
                {child}
              </div>
            );
          })}
        </Fragment>
      ));
    },
    [children]
  );

  return (
    <div ref={containerRef} className={`${styles.marquee_container}`}>
      <div
        className={`${styles.marquee}`}
        style={{ animationPlayState: isHover ? "paused" : "running" }}
      >
        <div className={`${styles.initial_child_container}`} ref={marqueeRef}>
          {Children.map(children, (child) => {
            return (
              <div
                className={`${styles.child}`}
                onMouseOver={() => setIsHover(true)}
                onMouseOut={() => setIsHover(false)}
              >
                {child}
              </div>
            );
          })}
        </div>
        {multiplyChildren(multiplier - 1)}
      </div>
      <div
        className={`${styles.marquee}`}
        style={{ animationPlayState: isHover ? "paused" : "running" }}
      >
        {multiplyChildren(multiplier)}
      </div>
    </div>
  );
}
