import * as React from "react";
import { Link } from "gatsby";
import logoImg from "../images/Lane7(TriColor).svg";
import lane7Video from "../videos/Lane7(Changes).mp4";

const pageStyles = {
  fontFamily: "Montserrat",
  fontWeight: 600,
};

const NavHeader = ({ pageTitle, children }) => {
  return (
    <div className="" style={pageStyles}>
      <div className={`row`}>
        <div className="col-sm-12 col-md-12">
          <video
            style={{ width: "100%", height: "200px", objectFit: "fill" }}
            autoPlay
            loop
            muted
          >
            <source src={lane7Video} type="video/mp4" />
          </video>
        </div>
      </div>
      <div class="d-flex flex-row justify-content-center align-items-center flex-wrap bd-highlight mb-3">
        <div class="bd-highlight">
          <Link to="/" className="navbar-brand mx-0">
            <img
              alt="Lane7"
              src={logoImg}
              width={200}
              height={120}
              className="d-inline-block align-text-top"
            />
          </Link>
        </div>
        <div class="bd-highlight">
          <h1 className="text-dark" style={pageStyles}>
            Diggital
          </h1>
        </div>
      </div>
    </div>
  );
};

export default NavHeader;
